<template>
	<view class="l-falls" :class="{ 'opacity': isLoad }" :style="{ height: maxHeight+'px' }">
		<item
		@renderBox="renderBox" 
		:class="{ 'opacity': !s.isRead }" 
		:style="'left:'+(s.left || 0)+'px;top:'+(s.top || 0)+'px;width:calc((100% - '+spacePX+'px) / '+column+');'"
		:dataInfo="s" 
		:index="i" 
		:key="i" 
		v-for="(s,i) in dataList"></item>
	</view>
</template>

<script>
	import item from './item.vue'

	export default {
		name: 'Falls',
		props: {
			dataList: {
				type: Array,
				default () {
					return []
				}
			},
			loadDataCount: {
				type: Number,
				default: 10
			},
			column: {
				type: Number,
				default: 2
			},
			spaceX: {
				type: Number,
				default: 9
			},
			spaceY: {
				type: Number,
				default: 9
			},
			padding: {
				type: Array,
				default () {
					return [12, 12]
				}
			}
		},
		components: {
			item
		},
		data() {
			return {
				childSizeList: [],
				heightList: [],
				maxHeight: 0,
				isLoad: true
			}
		},
		computed: {
			spacePX() {
				let {
					spaceX,
					column,
					padding
				} = this;
				return spaceX * (column - 1) + padding[0] + padding[1]
			}
		},
		mounted() {
			this.heightList = Array(this.column).fill(0);
		},
		watch: {
			dataList(e) {
				this.childSizeList = [];
			}
		},
		methods: {
			renderBox(params) {
				let _this = this;
				_this.childSizeList.push(params);
				if (_this.childSizeList.length === _this.loadDataCount) {
					_this.childSizeList.sort(_this.sortParams)
						.forEach((e) => {
							_this.onImgLoad(e)
						})
				}
			},
			sortParams(a, b){
				return a.index - b.index
			},
			onImgLoad(params) {
				this.isLoad && (this.isLoad = false);
				let _this = this;
				let {
					height,
					index,
					width
				} = params;
				let {
					spaceY,
					spaceX,
					dataList,
					padding,
					heightList
				} = _this;
				
				let min = Math.min.apply(null, heightList);
				let heightIndex = heightList.indexOf(min);
				let nHeight = spaceY + height;
				let top = heightList[heightIndex] + spaceY;
				let left = (spaceX + width) * heightIndex + padding[0];
				_this.heightList[heightIndex] += nHeight;
				_this.maxHeight = Math.max.apply(null, _this.heightList);

				_this.$set(_this.dataList[index], 'top', top)
				_this.$set(_this.dataList[index], 'left', left)
				_this.$set(_this.dataList[index], 'isRead', true)
			}
		}
	}
</script>

<style scoped lang="scss">
	.l-falls {
		width: 100%;
		position: relative;
	}
	
	.opacity {
		opacity: 0;
		z-index: -1;
	}
</style>
